import './ShoppingCart.css';
import React from 'react';
import Header from './../../Header/Header';
import Footer from './../../Footer/Footer';
import collections from './../../../apis/collections';
import history from './../../../history';
import { connect } from 'react-redux';
import { deleteCartItem, getModel } from './../../../actions';

class ShoppingCart extends React.Component {
  componentDidUpdate = () => {
    if (this.props.url && this.props.cart) {
      this.props.cart.forEach((el, index) => {
        document.querySelector(
          `#cardImg_${index}`
        ).src = `${this.props.url.downloadUrl}${this.props.url.bucket}/images/${el._id}.png?Authorization=${this.props.url.auth}`;
      });
    }
  };

  componentDidMount = (
    cart = this.props.cart,
    deleteCartItem = this.props.deleteCartItem,
    doc = window.document,
    showModal = function (cart, deleteCartItem, doc) {
      cart.forEach((item) => {
        deleteCartItem(item, cart);
      });
      doc.querySelector('#cart-modal').style.display = 'flex';
    }
  ) => {
    this.props.getModel();
    let totalPurchase = (
      this.subtotal() +
      this.tax() +
      this.discount()
    ).toString();
    window.paypal
      .Buttons({
        createOrder: function (data, actions) {
          // This function sets up the details of the transaction, including the amount and line item details.
          return actions.order.create({
            purchase_units: [
              {
                amount: {
                  value: totalPurchase,
                },
              },
            ],
          });
        },
        onApprove: function (data, actions) {
          // This function captures the funds from the transaction.
          return actions.order.capture().then(async function (details) {
            let listOfModels = [];
            for (let i = 0; i < cart.length; i++) {
              listOfModels.push(cart[i]._id);
            }
            await collections.post('/', {
              gId: window.gapi.auth2.getAuthInstance().currentUser.le.rt.NT,
              acquiredModels: listOfModels,
            });
            showModal(cart, deleteCartItem, doc);
          });
        },
      })
      .render('#paypal-button-container');
    window.paypal
      .Buttons({
        createOrder: function (data, actions) {
          // This function sets up the details of the transaction, including the amount and line item details.
          return actions.order.create({
            purchase_units: [
              {
                amount: {
                  value: totalPurchase,
                },
              },
            ],
          });
        },
        onApprove: function (data, actions) {
          // This function captures the funds from the transaction.
          return actions.order.capture().then(async function (details) {
            let listOfModels = [];
            for (let i = 0; i < cart.length; i++) {
              listOfModels.push(cart[i]._id);
            }
            await collections.post('/', {
              gId: window.gapi.auth2.getAuthInstance().currentUser.le.rt.NT,
              acquiredModels: listOfModels,
            });
            // this.props.cart.forEach((item) => {
            //   this.props.deleteCartItem(item, this.props.cart);
            // });
            // showModal();
          });
        },
      })
      .render('#paypal-button-containerm');
  };

  summaryRenderAdmin = (cart) => {
    return cart.map((item, idx) => {
      return (
        <div className="item list-item" key={idx}>
          <div className="ui tiny image">
            <img id={`cardImg_${idx}`} alt="" />
          </div>
          <div className="middle aligned content content-name">
            {item.modelName}
          </div>
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <div className="item-right-floated-options">
              <div
                className="item-delete-button"
                onClick={() => this.props.deleteCartItem(item, this.props.cart)}
              >
                <i className="trash icon trash-icon" />
              </div>
            </div>
            <div>{`$${item.modelPrice}`}</div>
          </div>
        </div>
      );
    });
  };

  subtotal = () => {
    let subtotal = 0;
    for (let i = 0; i < this.props.cart.length; i++) {
      subtotal += this.props.cart[i].modelPrice;
    }
    return subtotal;
  };

  tax = () => {
    let tax = 0;
    return tax;
  };

  discount = () => {
    let discount = 0;
    return discount;
  };

  render() {
    console.log(this.props.cart);
    return (
      <div className="cart-wrapper">
        <div className="cart-container">
          <Header />
          <div className="cart-gallery">
            <div className="summary-wrapper">
              <div className="ui divided items summary-container">
                {this.summaryRenderAdmin(this.props.cart)}
              </div>
              <div
                className="ui divider"
                style={{ marginLeft: '20px', marginRight: '20px' }}
              ></div>
              <div className="summary-sum">
                <strong style={{ paddingRight: '3px' }}>Subtotal:</strong>
                {`$${this.subtotal()}`}
              </div>
            </div>
            <div className="subtotal-container">
              <div className="ui padded segment total-segment">
                <div className="total-subtotal">
                  Subtotal: <div>{`$ ${this.subtotal()}`}</div>
                </div>
                <div className="total-tax">
                  Tax: <div>{`$ ${this.tax()}`}</div>
                </div>
                <div className="total-discount">
                  Discounts: <div>{`$ ${this.discount()}`}</div>
                </div>
                <div className="ui divider"></div>
                <div className="total-total">
                  <strong>Total: </strong>
                  {`$ ${this.subtotal() + this.tax() + this.discount()}`}
                </div>
              </div>
              <div id="paypal-button-container"></div>
            </div>
          </div>
          <div className="cart-gallery-mobile">
            <div className="ui divided items summary-container">
              {this.summaryRenderAdmin(this.props.cart)}
            </div>
            <div
              className="ui divider"
              style={{ marginLeft: '20px', marginRight: '20px' }}
            ></div>
            <div className="summary-sum">
              <strong style={{ paddingRight: '3px' }}>Subtotal:</strong>
              {`$${this.subtotal()}`}
            </div>
            <div className="ui padded segment total-segment">
              <div className="total-subtotal">
                Subtotal: <div>{`$ ${this.subtotal()}`}</div>
              </div>
              <div className="total-tax">
                Tax: <div>{`$ ${this.tax()}`}</div>
              </div>
              <div className="total-discount">
                Discounts: <div>{`$ ${this.discount()}`}</div>
              </div>
              <div className="ui divider"></div>
              <div className="total-total">
                <strong>Total: </strong>
                {`$ ${this.subtotal() + this.tax() + this.discount()}`}
              </div>
            </div>
            <div id="paypal-button-containerm"></div>
          </div>
        </div>
        <div id="cart-modal" className="cart-modal">
          <div className="cart-modal-content">
            <p className="cart-modal-header">
              <i
                className="check circle outline icon"
                style={{ color: 'gray' }}
              />
              Purchase Successful
            </p>
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <div
                className="cart-modal-button"
                style={{ paddingRight: '2.5px !important' }}
              >
                <div
                  className="ui green button"
                  onClick={() => {
                    window.open(
                      `${window.location.protocol}//${window.location.host}/MyCollection`,
                      '_self'
                    );
                  }}
                >
                  Go to Collection
                </div>
              </div>
              <div
                className="cart-modal-button"
                style={{ paddingLeft: '2.5px !important' }}
              >
                <div
                  className="ui green button"
                  onClick={() => {
                    window.open(
                      `${window.location.protocol}//${window.location.host}/`,
                      '_self'
                    );
                  }}
                >
                  Go to Main Page
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { user: state.user, cart: state.cart.cart, url: state.model.url };
};

export default connect(mapStateToProps, { deleteCartItem, getModel })(
  ShoppingCart
);
