import './MyCollection.css';
import React from 'react';
import Header from './../../../Header/Header';
import Footer from './../../../Footer/Footer';
import logImage from './../../../../media/images/logImage.png';
import { connect } from 'react-redux';
import {
  getAcquiredModels,
  checkIfAcquired,
  getModel,
} from './../../../../actions';

class MyCollection extends React.Component {
  componentDidMount = () => {
    if (!this.props.user.isUserLoggedIn) {
      document.querySelector('#log-modal2').style.display = 'flex';
    }

    if (this.props.user.isUserLoggedIn) {
      this.props.checkIfAcquired(this.props.user.userData.gId).then(() => {
        this.props.getAcquiredModels(this.props.acquiredModels.acquiredModels);
      });
    }

    this.props.getModel();
  };

  componentDidUpdate = () => {
    if (this.props.url && this.props.acquiredModelsToRender) {
      this.props.acquiredModelsToRender.data.forEach((el, index) => {
        document.querySelector(
          `#cardImg_${index}`
        ).src = `${this.props.url.downloadUrl}${this.props.url.bucket}/images/${el._id}.png?Authorization=${this.props.url.auth}`;
        if (el.modelFormats.includes('stl')) {
          document
            .querySelector(`#stl-download-button${index}`)
            .addEventListener('click', () => {
              window.open(
                `${this.props.url.downloadUrl}${this.props.url.bucket}/files/${el._id}.stl?Authorization=${this.props.url.auth}`,
                '_blank'
              );
            });
        }
        if (el.modelFormats.includes('obj')) {
          document
            .querySelector(`#obj-download-button${index}`)
            .addEventListener('click', () => {
              window.open(
                `${this.props.url.downloadUrl}${this.props.url.bucket}/files/${el._id}.obj?Authorization=${this.props.url.auth}`,
                '_blank'
              );
            });
        }
        if (el.modelFormats.includes('mtl')) {
          document
            .querySelector(`#mtl-download-button${index}`)
            .addEventListener('click', () => {
              window.open(
                `${this.props.url.downloadUrl}${this.props.url.bucket}/files/${el._id}.mtl?Authorization=${this.props.url.auth}`,
                '_blank'
              );
            });
        }
      });
    }
  };

  renderListing = () => {
    if (!this.props.acquiredModelsToRender) {
      return (
        <div className="ui active inverted dimmer listing-lazy-load">
          <div className="ui text loader">Loading Collection...</div>
        </div>
      );
    } else {
      return this.props.acquiredModelsToRender.data.map((element, idx) => {
        return (
          <div className="item" key={idx}>
            <div className="ui small image">
              <img id={`cardImg_${idx}`} alt="" />
            </div>
            <div className="middle aligned content">
              <div className="header">{element.modelName}</div>
              <div className="extra">
                {element.modelFormats.includes('stl') ? (
                  <div
                    className="ui right floated button mycollection-action-button"
                    id={`stl-download-button${idx}`}
                  >
                    Download .stl
                  </div>
                ) : null}
                {element.modelFormats.includes('obj') ? (
                  <div
                    className="ui right floated button mycollection-action-button"
                    id={`obj-download-button${idx}`}
                  >
                    Download .obj
                  </div>
                ) : null}
                {element.modelFormats.includes('mtl') ? (
                  <div
                    className="ui right floated button mycollection-action-button"
                    id={`mtl-download-button${idx}`}
                  >
                    Download .mtl
                  </div>
                ) : null}
              </div>
            </div>
          </div>
        );
      });
    }
  };

  render() {
    return (
      <div>
        <Header />
        <div id="log-modal2" className="log-modal">
          <div className="log-modal-content">
            <p className="log-modal-header">
              <i
                className="exclamation circle icon"
                style={{ color: 'gray' }}
              />
              To check you collection you need to Log In:
            </p>
            <div>
              <img src={logImage} className="log-modal-image" alt="" />
            </div>
            <div className="log-modal-button">
              <div
                className="ui green button"
                onClick={() => {
                  document.querySelector('#log-modal2').style.display = 'none';
                  window.open(
                    `${window.location.protocol}//${window.location.host}`,
                    '_self'
                  );
                }}
              >
                Go to Main Page
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className="collection-title">My Collection</div>
          <div className="collection-content-container">
            <div className="ui items collection-content">
              {this.renderListing()}
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  console.log(state);
  return {
    user: state.user,
    acquiredModels: state.cart.acquiredModels,
    acquiredModelsToRender: state.cart.acquiredModelsToRender,
    url: state.model.url,
  };
};

export default connect(mapStateToProps, {
  getAcquiredModels,
  checkIfAcquired,
  getModel,
})(MyCollection);
