import axios from 'axios';
import history from './../history';
import models from './../apis/models';
import users from './../apis/users';
import collections from './../apis/collections';

export const createModel = (formValues, classification, formatArr) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);
    formValues = {
      ...formValues,
      modelClassification: classification,
      modelFormats: formatArr,
    };

    console.log(formValues.formatArr);

    delete formValues['modelFiles'];
    delete formValues['modelImages'];

    const config = {
      onUploadProgress: function (progressEvent) {
        var percentCompleted = Math.round(
          (progressEvent.loaded * 100) / progressEvent.total
        );
        document.querySelector(
          '#modal-upload-progress'
        ).innerHTML = `${percentCompleted}%`;
        document.querySelector(
          '#upload-bar'
        ).style.width = `${percentCompleted}%`;
        document
          .querySelector('#upload-bar-indicator')
          .setAttribute('data-percent', `${percentCompleted}`);
      },
    };

    console.log(formValues);

    const response = await models.post('/', formValues, config);
    console.log('POST REQUEST SUCCESSFULL');
    console.log(response.data.modelId);

    dispatch({
      type: 'CREATE_MODEL',
      payload: response.data.modelId,
    });
    // history.go('/admin');
  };
};

export const editModel = (
  formValues,
  classification,
  formatArr,
  images,
  files,
  id
) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);

    if (!files) {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        _id: id,
      };
    } else {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        modelPreview: files[0],
        _id: id,
      };
    }

    const response = await models.patch(`/${id}`, formValues);

    dispatch({ type: 'CREATE_MODEL', payload: response.data });
    history.go('/admin');
  };
};

