export const getModels = (classif) => {
  return async (dispatch) => {
    console.log(classif);
    if (!classif) {
      const response = await models.get('/');
      dispatch({ type: 'GET_MODELS', payload: response.data.data });
    } else {
      const response = await models.get(`/ByClassification/${classif}`);
      dispatch({ type: 'GET_MODELS', payload: response.data.data });
    }
  };
};

export const getModel = (id) => {
  return async (dispatch) => {
    // https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/modelfiles/
    // http://localhost:8000/api/v1/modelfiles/
    const response = await axios.get(
      `https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/modelfiles/`
    );

    if (!id) {
      dispatch({
        type: 'GET_MODEL',
        payload: {
          downloadUrl: response.data.downloadUrl,
          bucket: response.data.bucket,
          auth: response.data.auth,
        },
      });
    } else {
      dispatch({
        type: 'GET_MODEL',
        payload: {
          downloadUrl: response.data.downloadUrl,
          bucket: response.data.bucket,
          auth: response.data.auth,
          id: id,
        },
      });
    }
  };
};

